@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&family=Playfair+Display:wght@400;600&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --color-bg: #252525;
  --color-bg-variant: #463c52;
  --color-primary: #a986cc;
  --color--primary-variant: rgba(77, 181, 255, .4);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, .6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html,
body,
#root,
.app {
  background-color: #010026;
  display: flex;
  flex-direction: column;
  @apply text-white;

}
html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  margin-right: 10px;
}
::-webkit-scrollbar-corner {
  border: none;
  background: none;
}
::-webkit-scrollbar-thumb {
  background-color: rgb(59 130 246 / 0.5);
  border-radius: 3px;
  cursor: move;
}


a {
  color: var(--color-primary);
  transition: var(--transition);
}



@media (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }

  section > h2 {
    margin-bottom: 2rem;
  }
}